import React from 'react';
import {Howl} from 'howler';
import { CiPlay1, CiPause1 } from "react-icons/ci";

 
 



function Music(){
const sound = new Howl({
    
     
    // src:['https://www.soundhelix.com/examples/mp3/SoundHelix-Song-4.mp3'],    this is good but not matching with the vid
    src:['https://soundbible.com/mp3//45min_april_rainstorm-mike-koenig.mp3'],
    html5:true,
})



    return (
        <>
      
        <button onClick={()=> sound.play()}><CiPlay1 /></button>
        <button onClick={()=> sound.pause()}><CiPause1 /></button>
     


        </>
    )
}





export default Music;




 